h1,
h2,
h3,
h4,
h5 {
    margin: 0;
    padding: 0;

}

.container {
    display: block;
    margin: 0 auto;
    max-width: 1200px;
    margin-top: 55px;
}

.character {
    display: block;
    margin: 0 auto;

    &_img {
        border: 5px solid #F2F2F7;
        border-radius: 150px;
    }

    &_name {
        text-align: center;
        font-weight: 400;
        font-size: 48px;
    }
}

.info {
    font-weight: 500;
    font-size: 20px;
    text-align: center;
    letter-spacing: 0.15px;
    color: #8E8E93;
    margin-top: 50px;
    margin-bottom: 60px;
}

.infoWrapper {
    display: block;
    width: 413px;
    height: 230px;
    margin: 0 auto;

    &_params {
        border-bottom: 1px solid rgb(128, 128, 128, .2);
        margin-bottom: 10px;
    }

    &_params h4 {
        font-weight: 700;
        font-size: 16px;
        line-height: 24px;
        letter-spacing: 0.15px;
        color: #081F32;
    }

    &_params h5 {
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.25px;
        color: #6E798C;
    }
}

.goBack {
    display: flex;
    width: 108px;
    align-items: center;
    justify-content: space-between;
    margin-top: 21px;
    margin-left: 50px;
    transition: .4s ease all;
}

.goBack h5 {
    font-weight: 700;
    font-size: 18px;
    line-height: 21px;
    color: #000000;
}

.goBack:hover {
    transform: translateX(-10px);
}

@media (max-width: 1200px) {
    .container {
        margin-top: 30px;
    }
}

@media (max-width: 993px) {
    .character_img {
        width: 250px;
    }
}

@media (max-width: 768px) {
    .goBack {
        margin-top: 23px;
        margin-left: 24px;
    }
}

@media (max-width: 600px) {
    .character {
        &_img {
            width: 150px;
        }

        &_name {
            font-weight: 400;
            font-size: 32px;
            line-height: 38px;
            margin-top: 34px;
        }
    }

    .infoWrapper {
        width: 285px;
    }

    .info {
        margin-top: 35px;
        margin-bottom: 15px;
    }
}


@media (max-width: 420px) {
    .info {
        margin-top: 35px;
        margin-bottom: 15px;
        margin-left: 24px;
        text-align: start;
    }
}